import React, {useState} from 'react'
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink, NavbarText } from 'reactstrap';

function setArt(art = 'characters') {
    return art
}

export {setArt}

export default function Header() {
    const [collapsed, setCollapsed] = useState(true),
          //[isOpen, setIsOpen] = useState(false),
          toggleNavbar = () => {setCollapsed(!collapsed)}

    return (
        <>
            <Navbar
                color='faded'
                light
                expand='md'
                className='header'>
                <NavbarBrand
                    className="mr-auto">
                    Game Of Thrones
                </NavbarBrand>

                <NavbarToggler
                    onClick={toggleNavbar}
                    >
                </NavbarToggler>

                <Collapse
                    isOpen={!collapsed} navbar>
                        <Nav navbar className='flex space-between'>
                            <NavItem>
                                <NavLink
                                    onClick={() => {setArt('characters')}}>
                                    Characters
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                    onClick={() => {setArt('houses')}}>
                                    Houses
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink>
                                    Books
                                </NavLink>
                            </NavItem>
                        </Nav>
                </Collapse>
            </Navbar>
        </>
    )
}


