import React, {Component} from 'react'
import {Button, ListGroup, ListGroupItem} from "reactstrap";


export default class ItemDetails extends Component {

    state = {
        name: '',
        gender: '',
        born: '',
        died: '',
        culture: '',

        autors: '',
        numberOfPages: '',
        released: '',
        country: ''
    }

    updateItemDetails = () => {
        const {gender, born, died, culture, autors, numberOfPages, released, country} = this.props.item
        this.setState({gender, born, died, culture, autors, numberOfPages, released, country})
        console.log('HOOK: Item details have been updated')
    }

    componentDidMount() {
        console.log('HOOK: Item details are mounted')
        this.updateItemDetails()
    }

    render() {
        if (this.props.i !== this.props.selectedItem) {
            console.log("Wrong")
            return (<></>)
        }
        console.log("rendering Item details...")
        const art = this.props.art
        const { gender, born, died, culture, autors, numberOfPages, released, country } = this.state
        let firstSpan, firstSpanValue, secondSpan, secondSpanValue, thirdSpan, thirdSpanValue, fourthSpan, fourthSpanValue
            if (art === 'characters') {
                firstSpan = 'Gender' 
                firstSpanValue = gender
                secondSpan = 'Born'
                secondSpanValue = born
                thirdSpan = 'Died'
                thirdSpanValue = died
                fourthSpan = 'Culture'
                fourthSpanValue = culture
            }
            if (art === 'books') {
                firstSpan = 'Autors'
                firstSpanValue = autors
                secondSpan = 'NumberOfPages'
                secondSpanValue = numberOfPages
                thirdSpan = 'Released'
                thirdSpanValue = released
                fourthSpan = 'Country'
                fourthSpanValue = country
            }
        return (

            <>
                <ListGroup className="mt20">
                    <ListGroupItem color='info' className='item-detailed-info flex space-between center ovh'>
                        <span>{firstSpan}</span>
                        <span>{firstSpanValue ? firstSpanValue : 'Unknown'}</span>
                    </ListGroupItem>
                    <ListGroupItem color='info' className='item-detailed-info flex space-between center ovh nowrap'>
                        <span>{secondSpan}</span>
                        <span>{secondSpanValue ? secondSpanValue : 'Unknown'}</span>
                    </ListGroupItem>
                    <ListGroupItem color='info' className='item-detailed-info flex space-between center ovh'>
                        <span>{thirdSpan}</span>
                        <span>{thirdSpanValue ? thirdSpanValue : 'Unknown'}</span>
                    </ListGroupItem>
                    <ListGroupItem color='info' className='item-detailed-info flex space-between center ovh'>
                        <span>{fourthSpan}</span>
                        <span>{fourthSpanValue ? fourthSpanValue : 'Unknown'}</span>
                    </ListGroupItem>
                </ListGroup>
                <Button 
                    className='mt10 float-right'>
                        Close
                </Button>
            </>

        )
    }
}