import Header from './header'
import RandomItem from './random-item'

import './App.css'
import { Component } from 'react'
import { Button } from 'reactstrap'
import ItemList from './item-list'




export default class App extends Component {

  state = {
    visible: false
  }

  toggleRandom = () => {
    this.setState({
      visible: !this.state.visible
    })
  }

  qwe = () => {
    if (this.state.visible) {
      return (
        <RandomItem/>
      )
    }
    else {
      return (
        <>
        </>
      )
    }
  }

  render() {
    const isActive = this.state.visible === true ? 'primary' : 'secondary'
    return (
      <>
        <Header/>

        <Button 
          color={isActive}
          className="margin btn-toggle-random mw400"
          onClick={this.toggleRandom}>
            Random Character
        </Button>
    
        <this.qwe
          closeRandom={this.closeRandom}/>

        <ItemList></ItemList>
      </>
    )
  }

}



