
import React, { Component } from "react";
import { ListGroup, ListGroupItem, Spinner, Button } from "reactstrap";


export default class RandomItem extends Component {

    state = {
        name: <Spinner size="sm" color="primary" />,
        gender: <Spinner size="sm" color="primary" />,
        born: <Spinner size="sm" color="primary" />,
        died: <Spinner size="sm" color="primary" />,
        culture: <Spinner size="sm" color="primary" />
    }

    componentDidMount() {
        console.log('mounting random item')
        this.timerId = setInterval(this.getRandomItem, 3000)

        //this.getRandomItem()
    }
    componentWillUnmount() {
        console.log('unmounting random item')
        clearInterval(this.timerId)
    }

    getRandomItem = () => {
        console.log("Updating random item...")
        fetch(`https://anapioficeandfire.com/api/characters/${Math.floor(Math.random() * 100 + 25)}`)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    name: data.name,
                    gender: data.gender,
                    born: data.born,
                    died: data.died,
                    culture: data.culture
                })
            })
            .catch(error => {
                this.setState({
                    name: "Error",
                    gender: "Error",
                    born: "Error",
                    died: "Error",
                    culture: "Error"
                })
            })
    }

    refreshRandomChartacter = (event) => {
        event.preventDefault()
        this.getRandomItem()
    }

    render() {
        console.log("rendering random item...")
        const {name, gender, born, died, culture} = this.state
        return (
            <ListGroup className="margin mw400">
                <ListGroupItem className="cardHeader">
                    <span>Random character:</span><br></br>
                    <h2 className="mt10">{name ? name : "Unknow"}</h2>
                    <Button 
                        color="basic" 
                        className="btn-refresh"
                        onClick={this.refreshRandomChartacter}>
                            <i className="fa fa-redo"></i>
                    </Button>
                </ListGroupItem>
                <ListGroupItem
                    className="flex space-between center">
                        <span>Gender</span>
                        <span>{gender ? gender : "Unknow"}</span>
                </ListGroupItem>
                <ListGroupItem
                    className="flex space-between center">
                        <span>Born</span>
                        <span>{born ? born : "Unknow"}</span>
                </ListGroupItem>
                <ListGroupItem
                    className="flex space-between center">
                        <span>Died</span>
                        <span>{died ? died : "Unknow"}</span>
                </ListGroupItem>
                <ListGroupItem
                    className="flex space-between center">
                        <span>Culture</span>
                        <span>{culture ? culture : "Unknow"}</span>
                </ListGroupItem>
            </ListGroup>
        )
    
    }
}
