import { Component } from "react";
import {ListGroup, ListGroupItem, Pagination, PaginationItem, PaginationLink} from "reactstrap"
import ItemDetails from './item-details'
import {setArt} from './header'


export default class ItemList extends Component {

    state = {
        itemList: [],
        selectedItem: '',
        page: 1,
        art: setArt()
    }

    componentDidMount() {
        console.log("Item list is mounted")
        this.getItemList()
    }

    getItemList(page = 1) {
        fetch(`https://anapioficeandfire.com/api/${this.state.art}?page=${page}`)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    itemList: data,
                    selectedItem: null,
                    page: page
                })
            })
        console.log("Item list has been updated")
    }

    expandItem(event) {
        if (event.target.classList.contains('btn')) {
            event.target.parentElement.classList.toggle('collapsedItem')
            event.target.parentElement.classList.toggle('expandedItem')
        }
        if (!event.target.classList.contains('Item-detailed-info') && !event.target.classList.contains('btn')) {
            event.target.classList.toggle('collapsedItem')
            event.target.classList.toggle('expandedItem')
        }
    }

    onItemSelected = (id, e) => {
        if (id !== this.state.selectedItem) {
            this.setState({
                selectedItem: id
            })
        }

        this.expandItem(e)
    }

    ItemDetails = (i, item) => {
        if (i === this.state.selectedItem) {
            return (
                <ItemDetails
                    item = {item}
                    art = {this.state.art}
                    i = {i}
                    selectedItem = {this.state.selectedItem}>                
                </ItemDetails>
            )
        }
    }


    renderItemList(arr, props) {

        return arr.map((item, i) => {
            return (
                <ListGroupItem 
                    className='collapsedItem'
                    key={i}
                    onClick={(e) => this.onItemSelected(i,e)}>
                        {item.name ? item.name : "Unknown"}

                        {this.ItemDetails(i, item)}

                </ListGroupItem>
            )   
        })
    }



    renderPagination(page) {
        
        let newArr = []
        let min = page === 1 ? page : (page === 2 ? page -1 : page -2),
              max = page === 1 ? page + 5 : (page === 2 ? page +4 : page +3)
              if (this.state.itemList.length < 10) {
                  max = page + 1
              }

        for (let i = min; i < max; i++) {



            const qwe = (i) => {
                const isActive = this.state.page === i ? true : false
                return (
                    <PaginationItem 
                        id={i}
                        active = {isActive}>
                        <PaginationLink
                            onClick={() => this.getItemList(i)}>
                                {i}
                        </PaginationLink>
                    </PaginationItem>
                )
            }
            newArr.push(qwe(i))
        }
        return newArr
    }

    render() {
        const {itemList} = this.state
        const items = this.renderItemList(itemList)
        const pagination = this.renderPagination(this.state.page)
        const isDisabled = this.state.page === 1 ? true : false

        console.log('rendering Item list...')

        return (
            <ListGroup className="all-Item-list margin mw400 ovh">
                <ListGroupItem className="tac">
                    <h3>All {this.state.art}</h3>
                </ListGroupItem>
                    {items}
                <ListGroupItem>
                <Pagination>
                    <PaginationItem>
                        <PaginationLink
                            first
                            disabled={isDisabled}
                            onClick={() => this.getItemList(1)}>
                        </PaginationLink>                    
                    </PaginationItem>
                    <PaginationItem>
                        <PaginationLink 
                            previous
                            disabled={isDisabled}
                            onClick={() => this.getItemList(this.state.page - 1)}>
                        </PaginationLink>
                    </PaginationItem>

                    {pagination}

                    <PaginationItem>
                        <PaginationLink 
                            next
                            onClick={() => this.getItemList(this.state.page + 1)}>
                        </PaginationLink>
                    </PaginationItem>
                    <PaginationItem>
                        <PaginationLink 
                            last
                            onClick={() => this.getItemList(100)}>
                        </PaginationLink>
                    </PaginationItem>
                </Pagination>
                </ListGroupItem>
            </ListGroup>
        )
    }
}